import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
// import Item from './components/Item'
import Markdowneditor from './components/Mardowneditor'

class App extends Component {

	state = {
		markdownText : 'Type some Markdown'
	}

	componentDidMount(){
		console.log("truely mounted");
	}

	renderMarkdown(event){
		this.setState({markdownText : event.target.value});
	}

	render() {
		return (
		<div className="App">
			<header className="App-header">
				<img src={logo} className="App-logo" alt="logo" />
				<h1 className="App-title">Welcome to React</h1>
			</header>
			{/* <Item/> */}
			<textarea
				onChange={e => this.renderMarkdown(e)}
				defaultValue="Type some Markdown"
				width = "800px"
				height= "300px">
			</textarea>
			<h2 id="hello">header</h2>
			<Markdowneditor source={this.state.markdownText}/>
		</div>
	);
	}
}

export default App;



